'use strict';
import axios from 'axios';
import {config} from '../app/components/config';
import {isTestApiKey} from '../app/components/api-key-utils';

export function sendBeaconData(url, data) {
  const options = {
    responseType: 'json',
    timeout: 5000,
    headers: {
      'content-type': 'application/json',
      'Accept': 'application/json',
    },
  };

  return url !== '' ? axios.post(url, data, options) : Promise.resolve();

}

export function collectAllData(beaconData, clientData, batteryData) {
  return Object.assign(beaconData, clientData, batteryData);
}

export function determineBeaconURL(apiKey) {

  if(typeof apiKey === 'undefined' || apiKey === '' || apiKey === null) {
    return '';
  }

  if (isTestApiKey(apiKey)) {
    return config.beaconStorageUrlNonProd;
  } else {
    return config.beaconStorageUrlProd;
  }

}

