import './td.html';
import postRobot from '../../../post-robot-wrapper';
import { manageBeaconIDCookie } from '../../../beacon/beacon-id-manager';
import { getParentDomain } from '../utils';
import { collectAllData, sendBeaconData, determineBeaconURL } from '../../../beacon/beacon-sender';
import {config} from '../config';
import {addAccertifyScript, getAccertifyId, isAccertifyLoaded} from './accertify';
import {getAccertifyConfig} from './accertify-config-getter';

// Create a postRobot listener/emitter tied to the parent window and domain only
const clientDomain = document.referrer === '' ? 'file://' : getParentDomain();

// The component listener receives initialization events from the domain but any window
const clientListener = postRobot.listener({ window: window.parent, domain: clientDomain });
const componentListener = postRobot.listener({ domain: config.domain });

/**
 * Sends the beacon data to the beacon service
 */
clientListener.on('sendBeaconData', handleBeaconData);
componentListener.on('sendBeaconData', handleBeaconData);

let components = {
  beacon: {
  }
};

const beaconSessionId =  manageBeaconIDCookie(window);

/**
 * converting array properties to string.
 * @param value
 * @returns {string}
 */
export function convertArrayToString(value) {
  let updatedValue = '';
  if(value){
    updatedValue = JSON.stringify(value).replace(/\s|\[|\]/g,'');
  }
  return updatedValue;
}

/**
 * Extract the first value from nested array
 * @param value
 * @returns {Array}
 */
export function extractFirstValueFromArray(value) {
  let updatedArray = [];
  if(typeof value == 'object' && value) {
    value.forEach(function (val) {
      updatedArray.push(val[0]);
    });
  }
  return updatedArray;
}

/**
 * Verify the property type and accepts only array/object
 * @param value
 * @returns {Array}
 */
export function verifyPropertyType(value) {
  let updatedArray = [];
  if(typeof value == 'object' && value) {
    updatedArray = value;
  }
  return updatedArray;
}

/**
 * Updating the finger print object
 * @param fingerPrint
 */
export function updateFingerPrintProperties(fingerPrint) {
  fingerPrint['timeZoneOffset'] = fingerPrint.timezoneOffset;
  fingerPrint['timeZone'] = fingerPrint.timezone;

  // converting array to string.
  // replacing [] to empty
  fingerPrint['availableScreenResolution'] = convertArrayToString(fingerPrint.availableScreenResolution);
  fingerPrint['fonts'] = convertArrayToString(fingerPrint.fonts);
  fingerPrint['touchSupport'] = convertArrayToString(fingerPrint.touchSupport);

  // extracting the first value from nested array
  fingerPrint['plugins'] = extractFirstValueFromArray(fingerPrint.plugins);
  fingerPrint['enumerateDevices'] = verifyPropertyType(fingerPrint.enumerateDevices);

  // already available or not required for Beacon team so removing before sending to API
  delete fingerPrint['screenResolution'];
  delete fingerPrint['timezone'];
  delete fingerPrint['timezoneOffset'];
  delete fingerPrint['webgl'];
  delete fingerPrint['canvas'];

  return fingerPrint;
}

/**
 * handleBeaconData collects and sends beacon data
 * @param {Event} event
 * @returns {Promise<T | void>}
 */
export function handleBeaconData(event) {

  const {clientData, eventName, sourceId, batteryData, fingerPrintData} = event.data;

  const beaconDetails = components['beacon'];

  const deviceTransactionID = isAccertifyLoaded(window) ? getAccertifyId(window) : '';

  const beaconData = {
    beaconID: beaconSessionId + '_' + fingerPrintData.deviceFingerprint,
    event: eventName,
    apiKey: beaconDetails.apiKey,
    customerID: beaconDetails.customerID,
    paymentSourceID: sourceId,
    deviceTransactionID: deviceTransactionID
  };

  const updatedFingerPrintData = updateFingerPrintProperties(fingerPrintData);
  const updatedBeaconData = Object.assign({},beaconData, updatedFingerPrintData);

  const allData = collectAllData(updatedBeaconData, clientData, batteryData);

  return sendBeaconData(determineBeaconURL(beaconDetails.apiKey), allData).then(() => {
    return Promise.resolve();
  }).catch(() => {
    return Promise.resolve();
  });

}

/**
 * Saves the api Key to the beacon component
 */
clientListener.on('sendBeaconInitialData', handleBeaconInitialData);

/**
 * handleBeaconApiKey checks to make sure apiKey is present in event data and stores it in beacon component
 * @param {Event} event
 * @returns {Promise}
 */
export function handleBeaconInitialData(event) {
  const {apiKey, customerID} = event.data;

  if (!apiKey) {
    return Promise.reject('Controller must receive an API Key.');
  }

  components['beacon'].apiKey = apiKey;
  components['beacon'].customerID = customerID;

  return getAccertifyConfig(apiKey).then((response) => {
    const {enabled, dvcf} = response.data;
    if(enabled) {
      addAccertifyScript(apiKey, dvcf);
    }
    return Promise.resolve();
  }).catch(() => {
    addAccertifyScript(apiKey);
    return Promise.resolve();
  });

}