'use strict';
import axios from 'axios';
import {isTestApiKey} from '../api-key-utils';

/**
 *
 * @param {string} apiKey
 * @returns {Promise<AxiosResponse<T>>}
 */
export function getAccertifyConfig(apiKey) {
  const isTestKey = isTestApiKey(apiKey);
  const dispatchKey = determineDispatchKey(isTestKey);
  const dispatchHost = 'api.digitalriver.com';
  const accertifyConfigUrl = `https://${dispatchHost}/cfs-accertify-beacon/${apiKey}?apiKey=${dispatchKey}`;

  const options = {
    maxRedirects: 0,
    responseType: 'json',
    timeout: 15000,
    headers: {
      'Content-Type': 'application/json',
    }
  };


  return axios.get(accertifyConfigUrl, options);
}

/**
 *
 * @param {boolean} isTestKey
 * @returns {string} dispatch key
 */
function determineDispatchKey(isTestKey) {
  if (isTestKey) {
    return 'pk_test_c62941b1b2d04d37938876b97440d930';
  } else {
    return '9ee2fdcfa6c7451aa011a21b3b7c4201';
  }
}